r'),_vm._v("\n\n祝福各位導生在人生大至可分成早中晚三階段，已經即將進入中階段，希望畢業後的您平安健康順心！"),_c('br'),_c('br'),_vm._v("\n身心靈保持正面與熱情！享受活出精彩的您！加油\n")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol",attrs:{"cols":"12","lg":"6","xl":"3"}},[_c('div',{staticClass:"exarea"},[_c('img',{staticClass:"icon",attrs:{"src":require("./assets/Images/teacher09.jpg"),"alt":""}}),_vm._v(" "),_c('h3',[_vm._v("徐淑芳"),_c('span',{staticClass:"wish2"},[_vm._v("老師")]),_c('span',{staticClass:"wish"},[_vm._v("的祝福"),_c('span',{staticClass:"emoji"},[_vm._v("☺")])])])]),_vm._v(" "),_c('h4',[_vm._v("\n恭喜同學們順利完成學業踏出校門，但請同學們謹記，畢業不是學習的結束，而是另一個學習旅程的開啟。\n祈願各位帶著我們滿心的祝福與殷切的叮嚀，在每一個學習歷程中用心感受，盡力付出，找到自己的存在價值及人生意義。"),_c('br'),_c('br'),_vm._v("\n在未來的學習旅程中，你們將靠自己的人品行止，受人尊重；靠自己的專業素養，立足社會；也將靠自己的終身學習，\n不落人後。最後，祝福畢業同學們能夠勇於做夢，也都能夠踏實築夢，開拓屬於自己的精彩人生。\n")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol",attrs:{"cols":"12","lg":"6","xl":"3"}},[_c('div',{staticClass:"exarea"},[_c('img',{staticClass:"icon",attrs:{"src":require("./assets/Images/teacher10.jpg"),"alt":""}}),_vm._v(" "),_c('h3',[_vm._v("謝文川"),_c('span',{staticClass:"wish2"},[_vm._v("老師")]),_c('span',{staticClass:"wish"},[_vm._v("的祝福"),_c('span',{staticClass:"emoji"},[_vm._v("☺")])])])]),_vm._v(" "),_c('h4',[_vm._v("\n謝文川老師給同學的畢業祝福"),_c('br'),_c('br'),_vm._v("\n心之所嚮 做最好的自己!\n")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol",attrs:{"cols":"12","lg":"6","xl":"3"}},[_c('div',{staticClass:"exarea"},[_c('img',{staticClass:"icon",attrs:{"src":require("./assets/Images/teacher11.jpg"),"alt":""}}),_vm._v(" "),_c('h3',[_vm._v("龔千芬"),_c('span',{staticClass:"wish2"},[_vm._v("老師")]),_c('span',{staticClass:"wish"},[_vm._v("的祝福"),_c('span',{staticClass:"emoji"},[_vm._v("☺")])])])]),_vm._v(" "),_c('h4',[_vm._v("\n各位畢業的同學們:"),_c('br'),_c('br'),_vm._v("\n首先恭喜你們畢業即將邁向人生新的階段，雖然今年有些不同，但仍無法阻擋你們成為社會新鮮人光榮一刻，期勉大家展開雙翼，"),_c('br'),_c('br'),_vm._v("\n迎向廣闊的天空飛翔，划著雙槳，航向無邊無際的海洋，向著希望，帶著激情，攜著堅強，勇敢地前進，祝福你們 鴻圖大展 珍重再見!\n龔千芬 敬上\n")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol",attrs:{"cols":"12","lg":"6","xl