m._v("\n        感謝你們在我們剛進來的時候，給予很多校園生活上的幫助，特別感謝二年級的菁菁、珩宇學姊，良信學長以及幹部群中的每一位。\n        另外，中秋烤肉，聖誕交換禮物等等都帶給我們很多的歡笑。\n        希望畢業後大家都能夠身體健康，事業起飛。愛你們：）\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v("J109256114蘇○青:")])]),_vm._v(" "),_c('h4',[_vm._v("\n        親愛的學長姐 "),_c('br'),_vm._v("\n        相聚即是有緣~學弟祝福你們\n        畢業時節，相逢又告別，歸帆又離岸，既是往日歡樂的終結，又是未來幸福的開端。生活如一條長河，願你是一葉執著的小舟；生活如一葉小舟，願你是一名風雨無阻的水手。祝你前程似錦，幸福如意！\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v("J109256126 曾○容:")])]),_vm._v(" "),_c('h4',[_vm._v("\n        Dear 學長姐們： "),_c('br'),_vm._v("\n        時間過的飛快，還記得剛入學時，學長姐們用心舉辦的迎新餐會，以及在課程與各項活動的協助，讓在職多年後再進入校園的我們，能快速進入狀況。這一年多來疫情限制了我們許多活動，但限制不了我們的祝福，相信學長姐們亦是對未來有所期許才選擇再回到校園，祝福你們皆能達成心中所想，畢業快樂～祝福你（妳）們。\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v("J109256130鄭○欣 ")])]),_vm._v(" "),_c('h4',[_vm._v("\n        親愛的學長學姊～ "),_c('br'),_vm._v("\n        雖然相處的時間不長，您們即將要畢業了，因疫情讓我們無法共同參與畢業典禮，仍衷心祝福學長姐們鵬程萬里，朝著自己的目標邁進，並更上一層樓！往後的日子皆能平安喜樂☺️～畢業快樂！\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v(" J109256132 涂○傑:")])]),_vm._v(" "),_c('h4',[_vm._v("\n        高智商的學長姊們： "),_c('br'),_vm._v("\n        轉眼間，學長姊邁入了畢業的尾聲，還很深刻的記得，對於剛入學的我們，協助熟悉校園的事務，以及舉辦各式各樣的活動，建立非常好的榜樣讓我們學習。此刻學長姊們即將畢業，也祝福學長姊們畢業快樂，夢想到手！。\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v(" J109256135 許○尹:")])]),_vm._v(" "),_c('h4',[_vm._v("\n      親愛的學長學姐們： "),_c('br'),_vm._v("\n      衷心祝您們在工作中事業有成，在生活中夢想成真!畢業快樂～🎉\n      ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}}