學長姐們在學業及經驗上的分享，不僅給予我們建議，也灌溉了我們成長的養分，期盼像學長姐們一樣優秀及擁有菁英般的典範，祝福學長姐們平安、健康，恭喜畢業了！\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v(" J109256120 黃○瑩 :")])]),_vm._v(" "),_c('h4',[_vm._v("\n        おめでとうございます 🎊\n        Great job 🌟٩(๑＞W＜@)۶🌟\n        BRAVO╰(* ´︶` *)╯ "),_c('br'),_vm._v("\n        又見六月鳳凰花🌹開季\n        捎來美好的祝福\n        給自信美麗+卓越非凡的「高智商」學長學姊們 "),_c('br'),_vm._v("\n        雖然相處時間不長，感謝🙏這一年的照顧 "),_c('br'),_vm._v("\n        祝福學長學姊們未來一帆風順！畢業快樂！\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v(" J109256136 李○頴 :")])]),_vm._v(" "),_c('h4',[_vm._v("\n        高智商的學長姐您們好： "),_c('br'),_vm._v("\n        謝謝您們在學期間總是不嫌麻煩的舉辦各項聯誼活動，建立系上良好互動的傳統文化與彼此之間關係，創造留存人人擁有美好深刻的印象記憶。 "),_c('br'),_vm._v("\n        祝福各位學長姐們於各行各業領域，衝鋒陷陣的開創不凡的道路與奇蹟，未來帶領團隊走向充滿盼望的世界。\n        ")])]),_vm._v(" "),_c('b-col',{staticClass:"exareacol2",attrs:{"cols":"12","md":"6","xl":"3"}},[_c('div',{staticClass:"exarea2"},[_c('h3',[_vm._v(" J109256124 王○瑩 :")])]),_vm._v(" "),_c('h4',[_vm._v("\n        親愛的學長姐好："),_c('br'),_vm._v("\n        感謝有這個緣分與你們一起修學分修了一年，後來我也順利錄取高智商成為了你們的學妹，所謂「百年修得同船渡」能夠一起共同學習，可說是一種奇特的機遇。"),_c('br'),_vm._v("\n        感謝你們總是無私分享你們的在學校與工作上學習的經驗，尤其擔任班上幹部的幾位學長姐更是了不起，除了要工作、學業要兼顧之外，還是「無給職」為系上、班上付出，這一切的點點滴滴，在未來必定會讓你們更加卓越，也讓我感到非常的敬佩。\n        祝福各位學長姐"),_c('br'),_vm._v("\n        事業順心、身體健康、平安喜樂"),_c('br'),_vm._v("\n        畢業快樂!"),_c('br')])])],1)],1)]),_vm._v(" "),_c('div',{staticClass:"nkright"},[_c('b-button',{directives:[{name:"scroll-to",rawName:"v-scroll-to",value:('#element'),expression:"'#element'"}],staticClass:"nkbtn",attrs:{"variant":"warning"}},[_vm._v("回 頂 部")])],1),_vm._v(" "),_vm._m(4)])}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"banner"}},[_c('div',{staticClass:"container-fluid"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"content bigtitle animate__animated animate__swing"},[_c('img',{staticClass:"mainkv",attrs:{"src":require("./assets/Images/mainKV.png"),"alt":""}}),_vm._v(" "),_c('img',{staticClass:"mainkv-st",attrs:{"src":require("./assets/Images/mainKV_straight.png"),"alt":""}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{attrs:{"id":"graduation"}},[_c('div',{staticClass:"container-fluid about"},[_c('h1',{staticClass:"overviwh1"},[_vm._v("系主任致詞")]),_vm._v(" "),_c('h2',{staticClass:"overviwh2"},[_vm._v("108畢業快樂! "),_c('span',{staticClass:"overviwh3"},[_vm._v("108碩專的同學們 ")])]),_vm._v(" "),_c('span',{staticClass:"overviwh4"},[_vm._v("我是你們109學年度畢業時的系主任張添香，先跟大家恭喜:畢業快樂!")]),_c('br'),_vm._v("\n本系於民國90年開始招收第一屆學生，到今年七月結束，即將邁入21年。恭喜日間部碩士班與進推部碩專班畢業同學，"),_c('br'),_vm._v("\n你們是三校整併後，我們更名為智慧商務系，首屆入學且完成畢業的第一批學生。恭喜日間部大學部畢業班同學，你們是在本系還處於國立高雄應用科技大學資管系時期入學，在本系更名後的第二屆畢業生。你們是在畢業證書上，可以選擇印出原高雄應用科技大學，也可以選擇印出原資訊管理系的畢業生，你們經歷過資訊管理系時期的完整訓練，也有幸可以選修更名為智慧商務系後的人工智慧系列特色課程。今年五月剛好台灣碰到新冠肺炎疫情嚴峻，因為防疫政策，這是本系第一次以網頁專區的方式辦理畢業典禮，雖然形式不同，但系上偕同日間部系學會與碩專聯誼會的幫忙，希望能夠呈現出最符合你們期待的畢業典禮，陪伴同學留下最感動的篇章。相信，在這一個畢業專區的點滴，各位會感受到智慧商務系這個大家庭，對大家的溫暖、重視和祝福!"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n本系因為看到智慧科技的興起，以及傳統資管領域的限制，在併校後力求更名突破。因為是全國首創，在轉型的過程中，師生都需要付出很多的努力，才能被社會肯定。感謝各位願意跟系上一起努力，在今年本系大學畢業生的專題作品上，我們逐漸看到了許多應用智慧科技的亮點，也因此有組別申請到科技部大專生計畫的榮譽。同樣的，在今年本系畢業的日間部碩士班同學論文題目上，我們看到許多同學參與師長產學合作或科技部計畫，利用智慧科技協助處理醫療問題、金融問題與商務應用問題的研究成果；在今年本系畢業的碩專班同學論文題目上，很多同學也著手加入智慧科技的觀點協助解決工作和生活上的問題。"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n\n我們相信，各位帶著這一身的技藝投入職場，比起傳統資訊相關科系訓練出來的學生，會有更多且更優質的工作機會。"),_c('br'),_vm._v("\n我們系上有很多我們獨特的活動和傳統，像碩專班聯誼會舉辦的迎新送舊與論文題要研討會，不僅讓校內上下屆的情感和專業交流更緊密，更讓同學和已畢業校友串接，擴展工作和生活上可能的資源。在日間部的碩士班，師長們盡力給予同學與理論與實務結合訓練的課程與機會，提供優質的業界與系友講座，讓同學畢業就有實戰力。像日間部四技的同學，我們給予同學四年固定導師的制度，讓同學遭遇問題時有最信任且立即直接的協助；程式類課程的會考，讓大家挑燈夜戰的在電腦教室共同練題庫，產生了革命情感；還有課後輔導的趕場，專題發表的壓力和成長，另外，系上學姊長與學弟妹串連的家族制度，系學會期中期末辦的all pass禮，全系同學大顯身手的年度大活動資管之夜，秋天的系烤，冬天的全系包水餃活動等等，這一切發生在校園中的點滴，串出了屬於各位記憶中燕巢校區和建工校區的春夏秋冬。"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n現在，各位已經完成了階段性的任務，帶著師長的祝福，即將展開人生另一場精采的旅程。"),_c('br'),_vm._v("\n\n未來的路上，不可能永遠順利，可能會遇到困難和挫折，這裡送給各位同學三個錦囊妙計，"),_c('br'),_vm._v("\n第一是正向思考，樂觀面對，自然會產生正向循環。"),_c('br'),_vm._v("\n第二是保持謙卑學習的心境，真誠善良待人。"),_c('br'),_vm._v("\n第三是維持健康強健的體魄，才能面對各種挑戰。"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n\n當然，也歡迎各位同學常跟師長聯繫，有空回來跟師長們聊天分享點滴，我們是你們永遠的靠山和支持的力量。"),_c('br'),_vm._v("\n一日高智商人，終生高智商人，系上師長，真的很愛你們!最後，再次恭喜各位升格為智商系的系友，"),_c('br'),_vm._v("\n祝福各位，鵬程萬里，創造出屬於自己的精彩人生!我有信心，未來我一定會以各位為榮! "),_c('br'),_vm._v(" "),_c('br'),_vm._v(" "),_c('span',{staticClass:"overviwh5"},[_vm._v("再次祝福各位，畢業快樂，珍重再見! ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container-fluid nkust"},[_c('h2',[_vm._v("畢業生")]),_vm._v(" "),_c('h3',{staticClass:"skillh3"},[_vm._v("《108畢業生名單》")]),_vm._v(" "),_c('div',{staticClass:"skill"},[_c('div',{staticClass:"p0float"},[_c('li',[_vm._v("1.J108256102 李菁菁")]),_vm._v(" "),_c('li',[_vm._v("2.J108256103 林坤榮")]),_vm._v(" "),_c('li',[_vm._v("3.J108256106 吳俞鋒")]),_vm._v(" "),_c('li',[_vm._v("4.J108256107 楊良信")]),_vm._v(" "),_c('li',[_vm._v("5.J108256108 王耀德")]),_vm._v(" "),_c('li',[_vm._v("6.J108256109 許鈞嵐")]),_vm._v(" "),_c('li',[_vm._v("7.J108256110 趙珩宇")]),_vm._v(" "),_c('li',[_vm._v("8.J108256111 蘇志宏")]),_vm._v(" "),_c('li',[_vm._v("9.J108256112 張君義")]),_vm._v(" "),_c('li',[_vm._v("10.J108256113 蔡佩倫")]),_vm._v(" "),_c('li',[_vm._v("11.J108256115 呂明璇")])]),_vm._v(" "),_c('div',{staticClass:"p1float"},[_c('li',[_vm._v("12.J108256116 簡惠菁")]),_vm._v(" "),_c('li',[_vm._v("13.J108256117 孫維杰")]),_vm._v(" "),_c('li',[_vm._v("14.J108256119 吳培甄")]),_vm._v(" "),_c('li',[_vm._v("15.J108256120 洪士雯")]),_vm._v(" "),_c('li',[_vm._v("16.J108256121 陳柏曄")]),_vm._v(" "),_c('li',[_vm._v("17.J108256123 黃靖權")]),_vm.