<section id="graduation">

<div class="container-fluid about">

<h1 class="overviwh1">系主任致詞</h1>
<h2 class="overviwh2">108畢業快樂! <span class="overviwh3">108碩專的同學們 </span></h2>

<span class="overviwh4">我是你們109學年度畢業時的系主任張添香，先跟大家恭喜:畢業快樂!</span><br>
本系於民國90年開始招收第一屆學生，到今年七月結束，即將邁入21年。恭喜日間部碩士班與進推部碩專班畢業同學，<br>
你們是三校整併後，我們更名為智慧商務系，首屆入學且完成畢業的第一批學生。恭喜日間部大學部畢業班同學，你們是在本系還處於國立高雄應用科技大學資管系時期入學，在本系更名後的第二屆畢業生。你們是在畢業證書上，可以選擇印出原高雄應用科技大學，也可以選擇印出原資訊管理系的畢業生，你們經歷過資訊管理系時期的完整訓練，也有幸可以選修更名為智慧商務系後的人工智慧系列特色課程。今年五月剛好台灣碰到新冠肺炎疫情嚴峻，因為防疫政策，這是本系第一次以網頁專區的方式辦理畢業典禮，雖然形式不同，但系上偕同日間部系學會與碩專聯誼會的幫忙，希望能夠呈現出最符合你們期待的畢業典禮，陪伴同學留下最感動的篇章。相信，在這一個畢業專區的點滴，各位會感受到智慧商務系這個大家庭，對大家的溫暖、重視和祝福!<br> <br>
本系因為看到智慧科技的興起，以及傳統資管領域的限制，在併校後力求更名突破。因為是全國首創，在轉型的過程中，師生都需要付出很多的努力，才能被社會肯定。感謝各位願意跟系上一起努力，在今年本系大學畢業生的專題作品上，我們逐漸看到了許多應用智慧科技的亮點，也因此有組別申請到科技部大專生計畫的榮譽。同樣的，在今年本系畢業的日間部碩士班同學論文題目上，我們看到許多同學參與師長產學合作或科技部計畫，利用智慧科技協助處理醫療問題、金融問題與商務應用問題的研究成果；在今年本系畢業的碩專班同學論文題目上，很多同學也著手加入智慧科技的觀點協助解決工作和生活上的問題。<br> <br>

我們相信，各位帶著這一身的技藝投入職場，比起傳統資訊相關科系訓練出來的學生，會有更多且更優質的工作機會。<br>
我們系上有很多我們獨特的活動和傳統，像碩專班聯誼會舉辦的迎新送舊與論文題要研討會，不僅讓校內上下屆的情感和專業交流更緊密，更讓同學和已畢業校友串接，擴展工作和生活上可能的資源。在日間部的碩士班，師長們盡力給予同學與理論與實務結合訓練的課程與機會，提供優質的業界與系友講座，讓同學畢業就有實戰力。像日間部四技的同學，我們給予同學四年固定導師的制度，讓同學遭遇問題時有最信任且立即直接的協助；程式類課程的會考，讓大家挑燈夜戰的在電腦教室共同練題庫，產生了革命情感；還有課後輔導的趕場，專題發表的壓力和成長，另外，系上學姊長與學弟妹串連的家族制度，系學會期中期末辦的all pass禮，全系同學大顯身手的年度大活動資管之夜，秋天的系烤，冬天的全系包水餃活動等等，這一切發生在校園中的點滴，串出了屬於各位記憶中燕巢校區和建工校區的春夏秋冬。<br> <br>
現在，各位已經完成了階段性的任務，帶著師長的祝福，即將展開人生另一場精采的旅程。<br>

未來的路上，不可能永遠順利，可能會遇到困難和挫折，這裡送給各位同學三個錦囊妙計，<br>
第一是正向思考，樂觀面對，自然會產生正向循環。<br>
第二是保持謙卑學習的心境，真誠善良待人。<br>
第三是維持健康強健的體魄，才能面對各種挑戰。<br> <br>

當然，也歡迎各位同學常跟師長聯繫，有空回來跟師長們聊天分享點滴，我們是你們永遠的靠山和支持的力量。<br>
一日高智商人，終生高智商人，系上師長，真的很愛你們!最後，再次恭喜各位升格為智商系的系友，<br>
祝福各位，鵬程萬里，創造出屬於自己的精彩人生!我有信心，未來我一定會以各位為榮! <br> <br>
<span class="overviwh5">再次祝福各位，畢業快樂，珍重再見! </span>

