<h4 class="h4long">
傅振瑞老師給碩專同學的祝福 <br>

恭喜畢業同學們完成了兩年的碩士在職專班學業，兩年辛苦的求學生活，終於要畫下句點。今年一場突如其來的疫情，校園內少了許多有過去打卡拍照、互相擁抱、歡樂興奮的畢業場景，甚至無法當面互道珍重再見。期待的校園生活，甚至許多計劃也被因此打亂。疫情終究會過去，但是人生的腳步並不會停止，付出的心血、辛苦學習的酸甜、與同學間真摯的情感，這兩年的點點滴滴，都不會因為疫情而消失。而在在畢業這一刻，成為了每個人的生命中美好回憶與養分。
面對不可預期的未來，你有你的計劃，但是常常發現世界可能似乎另有計畫。我們也許改變不了已經發生的事情，但是我們可以試著改變與世界相處的方式。美國基督教神學家尼布爾，曾在19世紀30年代寫過一段「寧靜禱文」，與同學共勉之:<br><br>
請賜與我寧靜，好讓我能接受，我無法改變的事情；<br>
請賜與我勇氣，好讓我能改變，我能去改變的事情；<br>
請賜與我睿智，好讓我能區別，以上這兩者的不同。<br><br>

要知道畢業不是學習的結束，而是另一個嶄新的開始。心中隨時懷有擁有夢想，生命才會充滿樂趣。 腦中時時充滿思想，對世界充滿好奇與探索，生活才會豐富多彩。勇敢大膽的面向未來，人生才不會留下遺憾。願畢業生帶著師長的祝福和期許，揮動信心的翅膀，飛向開闊天空，開拓屬於自己的精彩人生！<br>
<br>
鵬程萬里 畢業快樂<br>

傅振瑞
</h4>

