<br>
嗨！親愛的高智商碩專班畢業生夥伴：<br>
正當畢業之際，嚴峻的疫情讓我們不能群聚，必須學著”獨處”才安全！在這段日子裡，我們見面，我們相聚，我們說話變得少之又少，以及應該有的畢典，應該有的放肆離愁歡聚，抵達里程碑終點應該有的儀式感，瞬間彷彿只能是一顆顆大大小小、泛著五彩繽紛的泡泡，承載著在校所有點點滴滴的情感和回憶飄向遠方。但是感謝有您！在我們交會的人生軌跡上，會牢牢記得曾經在此時此刻一起面對的世界級功課！
別過之後，期望大家能以來自於此（高智商）引以為傲並懷抱自信；
要有堅強而樂觀處世之心，即使受挫也能有勇氣再起出發；
要有寬容並理解待人之愛，懂得不卑不亢明白自在踏實過活。<br>
人生啊！到此一遊，在您的腦海記憶體，請仔細收藏我們共同經歷的畫面，倆倆不忘時，久久之後再遇，它們將是會心一笑的泉源，也是交會光芒燦爛的火種！
祝福您，乘風高飛，如心所嚮！！<br><br>

<span style="text-align:right;display:block;padding-right:10px">
美琦助教<br>
于建工校區系辦<br>
2021.7.13<br>
