.exarearow{margin:0 auto ;padding-left:0;}
.exareacol{min-height:550px;margin:5px 0 50px 0;padding:0px 0 20px 0 !important;background:#fff;border:3px solid #fefefe;height:100%;border-radius: 20px; box-shadow:0px 8px 8px rgba(0,0,0,.05);
